
footer{
    background: #111;
    height: auto;
    width: 100vw;
    font-family: "Open Sans";
    padding-top: 20px;
    color: #fff;
}

.footerContent {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    text-align: center;
}

.footerContent h3{
    font-size: 2rem;
    font-weight: 400;
    text-transform: capitalize;
    line-height: 3rem;
}

.footerContent p{
    max-width: 500px;
    margin: 10px auto;
    line-height: 28px;
    font-size: 14px;
}

.socials{
    list-style: none;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 1rem 0 3rem 0;
}

.socials li{
    margin: 0 20px;
}

.socials a{
    text-decoration: none;
    color: #fff;
}

.bottomFooter{
    background: #000;
    width: 100vw;
    padding: 20px 0;
    text-align: center;
}

.bottomFooter p{
    font-size: 16px;
    word-spacing: 2px;
    text-transform: capitalize;
}

.bottomFooter span{
    text-transform: uppercase;
    opacity: .4;
}